import React from "react";
import "./../assets/css/register.css";
import { useForm } from "react-hook-form";
import { Link, useHistory } from "react-router-dom";
import regBg from "./../assets/images/bgReg.jpg";
import useContexts from "../hooks/useContexts.js";
import { Button, Col, Form, Row, Spinner } from "react-bootstrap";
const Register = () => {
  const { UserRegister, loading, email } = useContexts();
  const history = useHistory();

  const { register, handleSubmit } = useForm();
  const onSubmit = (data) => {
    UserRegister(data, history);
  };
  if (email) {
    return <>{history.replace("/login")}</>;
  } else {
    return (
      <div className="container">
        <Row>
          <Col xs={12} md={6}>
            <div>
              <img src={regBg} className="w-100" />
            </div>
          </Col>
          <Col xs={12} md={6}>
            <h1 className="mt-5 pt-3 text-center">Register</h1>
            <p style={{ fontFamily: "cursive", color: "gray", textAlign: "center" }}>Let's begin The Journey.</p>
            <hr />
            <form className="registerForm mt-5" onSubmit={handleSubmit(onSubmit)}>
              <input
                className="form-control"
                required
                type="text"
                {...register("name", { required: true })}
                placeholder="Enter your name"
              />
              <input
                className="form-control"
                required
                type="email"
                {...register("email", { required: true })}
                placeholder="Enter your email"
              />
              <input
                className="form-control"
                type="password"
                required
                {...register("password", { required: true })}
                placeholder="Enter your password"
              />
              <button className="btn register-btn" style={{ marginLeft: "8em" }} type="submit">
                {loading ? (
                  <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                  </Spinner>
                ) : (
                  "Register"
                )}
              </button>
            </form>
            {/* <Form className="px-5">
              <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Control type="email" placeholder="Enter your email" />
                <Form.Text className="text-muted">
                  We'll never share your email with anyone else.
                </Form.Text>
              </Form.Group>

              <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Control type="password" placeholder="Password" />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Control type="password" placeholder="Retype Your Password" />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicCheckbox">
                <Form.Check type="checkbox" label="I Do Agree" />
              </Form.Group>
              <Button className="px-5 mb-3" variant="primary" type="submit">
                Submit
              </Button>
              <br />
            </Form> */}
            <Link to="/Login" style={{ textDecoration: "none", fontFamily: "cursive", marginLeft: "10em" }}>Already Have An Account?</Link>
          </Col>
        </Row>
      </div>
      // <div className="register-page">
      //   <div
      //     style={{ height: "100vh" }}
      //     className="d-flex mx-3 align-items-center justify-content-center"
      //   >
      //     <div className="formContainer">
      //       <div className="text-center">
      //         <img width="120px" src={avatar} alt="" />
      //       </div>
      //       <h2 className="text-center text-white">Please Register</h2>
      // <form className="registerForm" onSubmit={handleSubmit(onSubmit)}>
      //   <input
      //     className="form-control"
      //     required
      //     type="text"
      //     {...register("name", { required: true })}
      //     placeholder="Enter your name"
      //   />
      //   <input
      //     className="form-control"
      //     required
      //     type="email"
      //     {...register("email", { required: true })}
      //     placeholder="Enter your email"
      //   />
      //   <input
      //     className="form-control"
      //     type="password"
      //     required
      //     {...register("password", { required: true })}
      //     placeholder="Enter your password"
      //   />
      //   <button className="btn register-btn" type="submit">
      //     {loading ? (
      //       <Spinner animation="border" role="status">
      //         <span className="visually-hidden">Loading...</span>
      //       </Spinner>
      //     ) : (
      //       "Register"
      //     )}
      //   </button>
      // </form>
      //       <h6 className="text-center text-white">
      //         Already registered? <Link to="/login"> Please Login!</Link>
      //       </h6>
      //     </div>
      //   </div>
      // </div>
    );
  }
};

export default Register;
